.hidden-overflow-x {
    overflow-x: hidden;
}

.hidden-overflow-y {
    overflow-y: hidden;
}

@font-face {
    font-family: "Wafffle Story";
    src: url('fonts/Waffle\ Story.ttf')
}

#all {
    font-family: "Wafffle Story";
    color: white;
}

@keyframes showup-on-top {
    from {
        top: -100px;
    }
    to {
        top: 0px;
    }
}

.hide-on-phone {
    display: none;
}

.hide-on-phone-and-tablet {
    display: none;
}

#logo-banner-img {
    height: 100px;
    display: block;
    margin-right: auto;
    margin-left: 350px;
}

#sticky-bar {
    height: 100px;
    display: flex;
    justify-content: right;
    align-items: center;
    background-color: #da3644;
    transition: top .3s, background-color .3s;
    position: sticky;
    z-index: 100;
}

#sticky-ber-connect {
    font-size: 25px;
    padding: 10px 25px;
    border-radius: 55px;
    margin: auto 15px;
}

#sticky-bar-menu-btn {
    text-decoration: none;
    color: white;
    font-size: 50px;

    margin: auto 25px auto 25px;
}

#sticky-bar-menu {
    display: none;
}

#sticky-bar-menu-btn:hover #sticky-bar-menu{
    display: flex;
    flex-direction: column;
    position:absolute;

    background-color: #2D132C;
    width: 100%;
    left: 0;
    border-radius: 0px 0px 20px 20px;
    padding-bottom: 15px;
}

a {
    text-decoration: none;
}

a:visited {
    text-decoration: none;
}

a div:hover {
    border-bottom: #fad021 3px solid;
}

a:hover>div {
    border-bottom: #fad021 3px solid;
}

.sticky-section-link {
    color: white;
    font-size: 20px;
    margin: min(1vw, 15px) min(1vw, 15px);
    padding: min(1vw, 15px);
}

.section-link:hover {
    border-bottom: #fad021 5px solid;
}

.Icon-PNG {
    width: 40px;
    height: 40px;
    margin: 5px;
}

.TwitterButton,
.OpenseaButton {
    display: block;
    margin: 5px 5px;
    transition: background-image 0.4s;
    background-size: cover;
}

.TwitterButton:hover {
    cursor: pointer;
    border-bottom: #fad021 5px solid;
}

.OpenseaButton:hover {
    cursor: pointer;
    border-bottom: #fad021 3px solid;
}

header {
    font-size: 50px;
    color: #fad021;
}

p {
    font-size: min(5vw, 30px);
    font-family: sans-serif;
    font-weight: 600;
}

.rarity-description {
    width: min(80vw, 1000px);
    padding: 0px 5vw 25px 5vw;
    margin: 0px auto 50px auto;
    border-bottom: white 3px solid;
}

#rarity-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 2px;
}

.rarity-card {
    border: 8px solid #2D132C;
    background-color: white;
    color: #2D132C;
    display: flex;
    justify-content:space-between;
    align-items: center;
    border-radius: 5px;
    width: 60vw;
    height: 40vw;
    box-shadow: 0px 0px 5px #000000;
    margin: 5px 2vw;
    padding: 5px;
}

.rarity-title {
    font-size: min(5vw, 45px);
    font-weight: bold;
    text-align: center;
    border-bottom: none;
    width: 100%;
}

.rarity-rate {
    border-left: 2px solid #2D132C;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1vw;
    width: 30vw;
}

.drop-rate {
    margin: 0px;
    color: white;
    padding: 2vw 3vw;
    font-size: 10vw;
    align-self: center;
    border-radius: 5px;
}


.drop-chance {
    margin: 5px auto;
    font-size: min(7vw, 45px);
    width: 100%;
    font-weight: bold;
}

.rarity-profile {
    margin-top: 10px;
    border: 2px solid #2D132C;
    border-radius: 5px;
    height: 20vw;
    margin-left: 5px;
}

.member-link {
    padding: 25px 15px;
    color: white;
    display: flex;
    font-family: sans-serif;
    font-weight: bold;
    width: 250px;
}

.member-link div {
    color: #fad021;
    margin-left: 20px;
    font-size: 25px;
    margin-top: auto;
    margin-bottom: auto;
}

.member-link img {
    height: 40px;
}

.member-card {
    color: white;
    margin: 80px 0px;
    padding: 0px 0px 25px 25px;
    border-left: #fad021 3px solid;
}

.member-description {
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 25px;
}

.member-description p {
    margin: 0px;
    padding: 25px 0px 50px 25px;
    border-bottom: white 3px solid;
}

.member-description header {
    color: #fad021;
    font-size: min(4vw, 60px);
}

.member-profile {
    border: 10px solid white;
    box-shadow: 5px 5px 5px rgba(24, 22, 33, .5);
    border-radius: 25px;
    height: min(30vw, 400px);
    margin: 0px;
}

#footer-section {
    background-color: #2D132C;
    color: white;
    position: sticky;
    top: 0px;
    z-Index: 1;
    box-shadow: 0px 0px 5px rgba(24, 22, 33, .5);
    display: flex;
    padding: 25px 400px;
}

.footer-gap {
    background-color: #fad021;
    margin: 10px 50px;
    border-right: solid 3px white;
}

#footer-icon {
    width: 200px;
}

.Center {
    justify-content: center;
    align-self: center;
    vertical-align: middle;
    text-align: center;
}

.TitleHeader {
    padding: 40px 0px;
    font-weight: 900;
    text-align: center;
    vertical-align: middle;
    font-size: 10vw;
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px 2px #000000;
}

#nft-video {
    display: block;
    margin: -5px auto -60px auto;
    width: 75vw;
}

#mint-section header {
    font-size: min(8vw, 60px);
    color: #fad021;
    font-weight: 900;
    display: block;
}

#mint-section {
    background-color: #da3644;
    text-align: center;
}

#mint-area {
    width: auto;
}

#mint-description {
    border-bottom: #fad021 3px solid;
    padding: min(1vw, 25px) min(1vw, 50px);
    margin: 0px auto 10px auto;
    text-align: left;
    display: block;
    width: 80vw;
}

.mint-square-btn {
    width: 140px;
    height: 140px;
    font-size: 50px;
    border-radius: 25px;
}

#mint-controller {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 150px;
}


#mint-count {
    text-shadow: 0px 0px 5px black;
    font-size: 65px;
}

.mint-minted {
    margin: 10px auto;
    display: block;
}

.mint-btn {
    font-size: min(8vw, 80px);
    padding: 10px 50px;
    border-radius: 25px;
    margin: 10px auto 10px auto;
    display: block;
}

#mint-description p {
    margin: 5px;
}

#mint-description span {
    color: #fad021;
}

#roadmap-section .up-down-container {
    width: 80vw;
    text-align: center;
    margin: 0px auto 60px auto;
}

#roadmap-after-soldout header {
    color: #fad021;
}

#roadmap-after-soldout {
    display: block;
}

#roadmap-section {
    background-color: #C72C41;
}

.roadmap-bottom-line {
    border-bottom: #fad021 3px solid;
    margin: 40px 10vw 0px 10vw;
}

.roadmap-main-container {
    margin: 10px auto 80px auto;
}

.roadmap-container {
    margin: 1vw 10px;
    padding: 1vw 100px;
}

.roadmap-container2 {
    padding: 0px 20px;
    text-align: center;
}

.roadmap-bubble-container {
    background-color: #2D132C;
    color: white;
    text-align: center;
    border-radius: 50px;
    padding: 1vw 10px;
}

.roadmap-bubble-day {
    font-size: 8vw;
    margin: -1vw 0px -5px 0px;
    font-family: "Wafffle Story";
    height: 10vw;
}

.roadmap-bubble-month {
    font-size: 4vw;
    margin: 0px;
    font-family: "Wafffle Story";
}

.roadmap-bubble-time {
    font-size: 15px;
    color: #fad021;
    margin: 0px;
}

#about-section {
    background-color: #C72C41;
    text-align: center;

    display: block;

}

#about-img {
    width: 50vw;
    border: 20px solid #2D132C;
    margin: 0px auto 50px auto;
    border-radius: 25px;
}

#about-section header {
