    }

}

.NormalButton {
    font-family: "Wafffle Story";
    font-weight: 900;
    transition: background-color 0.4s;
    text-shadow: .1vw .1vw .1vw #000000;
    background-color: #2D132C;
    color: white;
    border-style: none;
    box-shadow: 0px 0px 5px black;
}

.NormalButton:hover {
    color: white;
    border-style: none;
    cursor: pointer;
    background-color: #fad021;
}

.DisabledButton {
    font-weight: 900;
    transition: background-color 0.4s;
    font-size: 1vw;
    padding: .5vw 1vw;
    text-shadow: 1px 1px 1px #000000;
    background-color: #2D132C;
    color: white;
    border-style: none;
    box-shadow: 0px 0px 10px black;
    cursor: not-allowed;
}

